.left{
    
    height: 30px;
    width: 30%;
    border-radius: 1rem 0 0 1rem;
    background-image: linear-gradient(to right, #FFC500, #FF9402);
    text-align: center;
}
.right{
    height: 30px;
    width: 30%;
    border-radius: 0 1rem 1rem 0;
    background-image: linear-gradient(to right, #FF7A00, #FE560A);
    text-align: center;
}
.btn-title{
    line-height:30px;
    color: #ffff;
    padding-top: 5px;
    font-size: 10px;
    font-weight: bold;
    /* box-sizing: border-box; */
}
.bottoms{
    height: 50px;
    position: fixed;
    bottom: 0;
    width: 100%;
    display: flex;
    align-items: center;
    background-color: #fff;

}
.tables{
    width: 40%;
}
